body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

  background: linear-gradient(to right, #A997DF, rgb(180, 164, 226));

}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;

}
@font-face {
  font-family: 'SEGA LOGO FONT';
  font-style: normal;
  font-weight: normal;
  src: local('SEGA LOGO FONT'), url(/testrobo/static/media/SEGA.12a3db8b.woff) format('woff');
  }
  

h1{
color:#592586;
font-family: 'SEGA LOGO FONT';
font-weight: 300;
}

.que{
  width:30px;
  height:30px;
  margin-right:20px;
  background:#5E2CA5;
  padding-top:5px;
  border-radius:50px;
  cursor:pointer;

  position:absolute;
  right:20px;
}

.describe{
  visibility: hidden;
  background:rgb(35, 105, 196);
  color:white;
  padding:15px;
  width:400px;
  /* margin-right:20px; */

  border-radius:15px;
  box-shadow: 0px 5px 5px rgba(53, 52, 52, 0.582);
  text-align: justify;

  position:absolute;
  z-index:3;
  right:20px;

  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;


}

button{
  height: 30px;
  width:60px;
  background:#B3A3E2;
  border-radius:15px;
  padding-bottom:5px;
  /* font-size: 0em; */


  border:0px;
  outline:none;
  cursor: pointer;

}
